.sidebar-layout {
  display: block;
  position: relative;
  margin-top: 75px;
  margin-bottom: 52px;
  @media (max-width: 1024px) {
    margin-top: 35px;
    margin-bottom: 35px;
  }
  .container {
    max-width: 1284px;
  }
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: 100%;
    position: relative;
  }
  &__content {
    width: 100%;
    max-width: calc(100% - 342px);
    padding-right: 72px;
    @media (max-width: 1024px) {
      max-width: calc(100% - 250px);
      padding-right: 40px;
    }
    @media (max-width: 767px) {
      max-width: calc(100% - 0px);
      padding-right: 0px;
      margin-bottom: 20px;
    }
  }
  &__sidebar {
    width: 342px;
    @media (max-width: 1024px) {
      width: 250px;
    }
    @media (max-width: 767px) {
      width: 342px;
    }
  }
  .layout-heading {
    margin-bottom: 40px;
  }
  &.article-detail {
    .sidebar-layout__sidebar {
      padding-top: 28px;
      @media (max-width: 767px) {
        padding-top: 0px;
      }
    }
  }
}

// btn layout
.start-back-btn {
  display: block;
  width: 100%;
  max-width: 100%;
  position: relative;
  margin: 27px 0px;
  p {
    color: var(--base-text-color);
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 180%; /* 28.8px */
    padding-bottom: 0px;
    margin-bottom: 10px;
    @media (max-width: 1024px) {
      margin-top: 20px;
    }
  }
  .btn-grp {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    margin: 0px -10px;
    justify-content: space-between;

    .btn {
      margin: 0px 10px;
      padding: 14px 22px;
      width: auto;
      max-width: calc((100% / 2) - 20px);
      min-width: 123px;
      justify-content: center;
      @media (max-width: 1024px) {
        min-width: initial;
        padding: 8px 16px;
      }
    }
  }
}
