.link-list {
  display: block;
  position: relative;
  ul {
    list-style: none;
    padding-left: 0px;
    padding-right: 0px;
    li {
      border-bottom: 1px solid #ccc;
      padding-bottom: 16px;
      margin-bottom: 18px;
      a {
        color: var(--base-text-color);
        display: block;
        position: relative;
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: 120%; /* 21.6px */
        transition: 0.4s ease;
        padding-right: 30px;
        svg {
          path {
            transition: 0.4s ease;
          }
        }
        &:hover {
          color: #3674b5;
          svg {
            path {
              stroke: #3674b5;
            }
          }
        }
        .icon {
          position: absolute;
          width: 8px;
          height: 14px;
          top: 50%;
          transform: translateY(-50%);
          right: 0;
          line-height: 0;
          svg {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
