@use "abstracts/mixins" as mixins;
.hero-banner {
  position: relative;
  padding: 77px 0 111px;
}

.hero-banner-img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
  }
}

.hero-banner-content {
  position: relative;
  color: white;
  h1 {
    @include mixins.font-size-rem(68);
    font-weight: 400;
    line-height: normal;
    color: white;
  }
  p {
    @include mixins.font-size-rem(22);
    font-weight: 400;
    line-height: normal;
    color: white;
  }
  &-inner {
    max-width: 800px;
    width: 70%;

    .search-box {
      position: relative;
      form {
        padding: 0;
        position: relative;
      }
      input {
        border-radius: 4px;
        border: 1px solid #fff;
        background: url(../images//icon-search-dark.svg) no-repeat rgba(255, 255, 255, 0.6);
        background-position: 18px 22px;
        backdrop-filter: blur(81.5px);
        padding: 27px 164px 27px 61px;
        @include mixins.font-size-rem(14);
        color: #07203b;
        font-weight: 400;
        @media (max-width: 500px) {
          padding: 27px 30px 27px 61px;
        }
        &::placeholder {
          color: #07203b;
          opacity: 1;
          @include mixins.font-size-rem(14);
        }
      }
      button {
        border-radius: 0 4px 4px 0;
        border-top: 1px solid #fff;
        border-right: 1px solid #fff;
        border-bottom: 1px solid #fff;
        background: #07203b;
        color: white;
        @include mixins.font-size-rem(16);
        font-weight: 600;
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        padding: 27px 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all 0.3s;
        &:hover,
        &:focus {
          background: #03101e;
        }
      }
    }
  }
}
@media only screen and (max-width: 1200px) {
  .hero-banner-content {
    h1 {
      @include mixins.font-size-rem(50);
    }
    p {
      @include mixins.font-size-rem(18);
    }
  }
}
@media only screen and (max-width: 768px) {
  .hero-banner {
    padding: 40px 0;
  }
  .hero-banner-content {
    &-inner {
      width: 100%;
    }
    h1 {
      @include mixins.font-size-rem(40);
    }
  }
}

@media only screen and (max-width: 500px) {
  .hero-banner {
    .hero-banner-content {
      &-inner {
        .search-box {
          button {
            position: relative;
            margin-top: 10px;
            width: 100%;
            border-left: 1px solid #fff;
            padding: 14px 50px;
          }
        }
      }
    }
  }
}
