.payment-success {
  display: block;
  width: 100%;
  max-width: 100%;
  position: relative;
  padding: 100px 0px;
  background: #f3f3f3;
  @media (max-width: 1024px) {
    padding: 50px 0px;
  }
  &__block {
    display: block;
    width: 100%;
    max-width: 571px;
    margin: 0 auto;
    position: relative;
    text-align: center;
    p {
      margin-bottom: 10px;
      padding: 0px;
    }
    img {
      display: block;
      margin: 0 auto 30px;
      width: 100%;
      max-width: 300px;
    }
    h2 {
      margin-bottom: 37px;
      font-size: 30px;
      font-style: normal;
      font-weight: 500;
      line-height: 28px; /* 93.333% */
    }
    .btn {
      margin-bottom: 20px;
      padding: 14px 25px;
    }
    .simple-link {
      color: #9da3a1;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 12px; /* 75% */
    }
  }
}
