.login-wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;
  min-height: 100vh;
  position: relative;
  &:after {
    content: "";
    display: block;
    width: 50%;
    height: 100%;
    top: 0;
    position: absolute;
    right: 0;
    @media (max-width: 767px) {
      display: none;
    }
  }
  &:before {
    content: "";
    display: block;
    width: 50%;
    height: 100%;
    background-image: url("../images/login-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
    position: absolute;
    left: 0;
    @media (max-width: 767px) {
      display: none;
    }
  }
  .login--block {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: 100%;
    position: relative;
    height: 100%;
    z-index: 1;
  }
  .half-col {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: calc(100% / 2);
    @media (max-width: 767px) {
      max-width: calc(100% / 1);
      padding: 40px 20px;
    }
    @media (max-width: 575px) {
      max-width: calc(100% / 1);
      padding: 40px 0px;
    }
  }
}

// content col styles
.content-col {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;
  position: relative;
  padding: 80px 0px;
  @media (max-width: 767px) {
    padding: 0px 0px;
  }
  &:after {
    @media (max-width: 767px) {
      content: "";
      display: block;
      width: calc(100% + 40px);
      height: 100%;
      background-image: url("../images/login-bg.png");
      background-repeat: no-repeat;
      background-size: cover;
      top: 0;
      position: absolute;
      left: 0;
      margin: 0px -20px;
    }
  }
  .content-wrapper {
    display: block;
    width: 100%;
    position: relative;
    z-index: 1;
  }
  .login-logo {
    display: block;
    width: 100%;
    max-width: 100%;
    position: relative;
    margin-bottom: 159px;
    @media (max-width: 767px) {
      margin-bottom: 40px;
    }
    img {
      max-width: 141px;
    }
  }
  .login-content {
    display: block;
    width: 100%;
    max-width: 526px;
    position: relative;
    * {
      color: #fff;
    }
    &__head {
      display: block;
      width: 100%;
      max-width: 100%;
      position: relative;
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      margin-bottom: 28px;
      padding-bottom: 24px;
      *:last-child {
        margin: 0px;
        padding: 0px;
      }
      h1 {
        margin-bottom: 5px;
        @media (max-width: 1024px) {
          font-size: 16px;
        }
      }
      p {
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
      }
    }
    &__details {
      display: block;
      width: 100%;
      max-width: 100%;
    }
    p {
      font-size: 18px;
      font-style: normal;
      font-weight: 600;
      line-height: 28.8px;
      margin-bottom: 10px;
      @media (max-width: 1024px) {
        font-size: 16px;
      }
    }
    ul {
      list-style: none;
      padding: 0px;
      margin: 0px;
      li {
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-bottom: 30px;
        position: relative;
        padding-left: 32px;
        @media (max-width: 1024px) {
          font-size: 16px;
        }
        &:before {
          content: "";
          display: inline-block;
          background-image: url(../images/fast-forward.svg);
          width: 24px;
          height: 25px;
          position: absolute;
          left: 0;
          top: -2px;
          line-height: 0;
          background-repeat: no-repeat;
        }
        &:last-child {
          margin-bottom: 0px;
        }
      }
    }
  }
}
.login-col {
  align-items: center;
  padding-left: 148px;
  @media (max-width: 1500px) {
    padding-left: 70px;
  }
  @media (max-width: 1200px) {
    padding-left: 40px;
  }
  @media (max-width: 767px) {
    padding-left: 0px;
    justify-content: center;
  }
  .login-form {
    display: block;
    width: 100%;
    max-width: 488px;
    position: relative;
  }
  .login-box-head {
    color: #07203b;
    font-family: Inter;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%; /* 22px */
    letter-spacing: 0.22px;
    margin-bottom: 10px;
  }
  .login-box-content {
    color: #07203b;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 25.6px */
    letter-spacing: 0.16px;
  }
  .login-box-logo {
    display: block;
    width: 100%;
    max-width: 100%;
    position: relative;
    margin-bottom: 14px;
    img {
      width: 121px;
    }
  }
  .login-box-content {
    display: block;
    width: 100%;
    margin-bottom: 32px;
    p {
      color: #07203b;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 160%; /* 25.6px */
      letter-spacing: 0.16px;
    }
    *:last-child {
      margin-bottom: 0px;
      padding: 0px;
    }
  }
  .login-box {
    display: block;
    width: 100%;
    max-width: 100%;
  }
}
