.rte-block {
  display: block;
  width: 100%;
  max-width: 100%;

  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 11px;
  }
  h1,
  h2 {
    margin-bottom: 20px;
  }
  p {
    padding-bottom: 0px;
    margin-bottom: 10px;
  }
  p + h1,
  p + h2,
  p + h3,
  p + h4,
  p + h5,
  p + h6 {
    padding-top: 19px;
  }
  ul,
  ol {
    padding-left: 20px;
    margin-bottom: 20px;
    margin-top: 20px;
    li {
      margin-bottom: 10px;
    }
  }
}
