.btn {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  width: auto;
  max-width: 100%;
  position: relative;
  color: var(--text-white);
  font-family: var(--primary-font);
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  cursor: pointer;
  padding: 16px 25px;
  border-radius: 4px;
  transition: 0.4s ease;
  @media (max-width: 1260px) {
    padding: 9px 16px;
  }
  &--payment {
    @media (max-width: 1260px) {
      padding: 16px 25px;
    }
  }
  &--login {
    @media screen and (max-width: 1260px) {
      font-size: 14px;
    }
  }
  &--sign-in {
    border-radius: 12px;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%; /* 20px */
    letter-spacing: 0.2px;
    text-align: center;
    padding: 24px 20px;
  }
  &--bg {
    &--blue {
      background: var(--bg-blue);
      color: #fff;
      transition: all 0.3s;
      &:hover {
        background: rgba(45, 84, 192, 0.7);
      }
    }
    &--yellow {
      background: #f6d84c;
      color: #07203b;
      &:hover {
        background: rgba(246, 216, 76, 0.7);
      }
    }
    &--white {
      background: var(--bg-white);
      color: #000;
      transition: all 0.3s;
      border: 1px solid #ccc;
      &:hover {
        border: 1px solid #000;
      }
    }
  }
  &--curved {
    border-radius: 50px;
  }
  .btn-logo {
    line-height: 0;
    margin-right: 4px;
  }
}
