.focus-slider {
  display: block;
  padding: 55px 0px;
  background: #f2f5ff;
  overflow: hidden;
  .in-focus-slider {
    &:after {
      content: "";
      width: 100%;
      height: 100%;
      background: #f2f5ff;
      position: absolute;
      top: 0;
      left: -100%;
      z-index: 9;
    }
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      background: #f2f5ff;
      position: absolute;
      top: 0;
      right: -100%;
      opacity: 0.6;
      z-index: 9;
      @media (max-width: 1024px) {
        opacity: 0;
      }
    }
  }
  .swiper {
    overflow: initial;
  }
  .swiper-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    position: absolute;
    top: -60px;
    right: 0;
    svg {
      path {
        transition: 0.4s ease;
      }
    }
    .prev {
      svg {
        transform: rotate(180deg);
      }
    }
    .prev,
    .next {
      cursor: pointer;
      &:hover {
        svg {
          path {
            stroke: #3674b5;
          }
        }
      }
    }
    .swiper-button-disabled {
      opacity: 0.2;
      svg {
        path {
          stroke: #000;
        }
      }
      &:hover {
        svg {
          path {
            stroke: #000;
          }
        }
      }
    }
  }
}
