@use "sass:math";

/// Mixin to convert pixel font-size to rem
/// @param {Number} $px - The font-size in pixels
/// @param {Number} $base - The base font-size in pixels (default: 16)
@mixin font-size-rem($px, $base: 16) {
    font-size: $px + px;
    font-size: math.div($px, $base) * 1rem;
}