.section-heading {
  display: block;
  width: 100%;
  max-width: 100%;
  position: relative;
  margin-bottom: 25px;
  *:last-child {
    margin-bottom: 0px;
  }
}
.layout-heading {
  display: block;
  width: 100%;
  max-width: 100%;
  &.has-border {
    border-bottom: 1px solid rgba(7, 32, 59, 0.2);
    padding-bottom: 23px;
  }
  .layout-heading-content {
    &.limited-width {
      width: 100%;
      max-width: calc(100% - 414px);
      @media (max-width: 1024px) {
        max-width: calc(100% - 0px);
      }
    }
    *:last-child {
      margin-bottom: 0px;
      padding-bottom: 0px;
    }
  }
}
