.login-box-input {
  display: block;
  width: 100%;
}
.login-box-button {
  display: block;
  width: 100%;
  max-width: 100%;
}

.single-col-form {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;

  margin-bottom: 23px;
}
.two-col-form {
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  margin: -10px;

  margin-bottom: 23px;
  .login-box-input {
    display: block;
    width: calc((100% / 2) - 20px);
    margin: 10px;
    @media (max-width: 575px) {
      width: calc((100% / 1) - 20px);
    }
  }
}

/* form common styles */
label {
  width: 100%;
  display: block;
  color: var(--base-text-color);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4; /* 16px */
  letter-spacing: 0.16px;
  margin-bottom: 15px;
}

input[type="text"],
input[type="email"],
input[type="password"],
select {
  border: 1px solid rgba(7, 32, 59, 0.2);
  background: rgba(255, 255, 255, 0.6);
  padding: 15px 25px;
  width: 100%;
  display: block;
  border-radius: 4px;
  @media (max-width: 1024px) {
    padding: 10px 15px;
  }
}
select {
  appearance: none;
  background-image: url(data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23131313%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E) !important;
  background-repeat: no-repeat !important;
  background-position: right 0.7rem top 50% !important;
  background-size: 0.65rem auto !important;
}

/* custom check box */
.custom-checkbox,
.custom-radio {
  display: block;

  input {
    padding: 0;
    height: initial;
    width: initial;
    margin-bottom: 0;
    display: none;
    cursor: pointer;
  }

  label {
    position: relative;
    cursor: pointer;
    color: rgba(7, 32, 59, 0.3);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 125% */
    margin-bottom: 0px;
  }
  input:checked + label {
    color: #07203b;
    font-size: 14px !important;
    font-style: normal;
    font-weight: 400 !important;
    line-height: 22px !important; /* 137.5% */
  }
  label:before {
    content: "";
    -webkit-appearance: none;
    border: 2px solid rgba(7, 32, 59, 0.2);
    background: rgba(255, 255, 255, 0.6);
    border-radius: 2px;
    width: 20px;
    height: 20px;
    display: inline-block;
    position: relative;
    vertical-align: middle;
    cursor: pointer;
    margin-right: 10px;
    margin-top: -2px;
  }
  input:checked + label:before {
    border: 2px solid #2d54c0;
  }
  input:checked + label:after {
    content: "";
    display: block;
    position: absolute;
    top: 5px;
    left: 7px;
    width: 6px;
    height: 11px;
    border: solid #2d54c0;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
  }
}
.custom-radio {
  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: -10px;
    margin-bottom: 0px;
    li {
      margin: 10px;
    }
  }
  label {
    color: rgba(7, 32, 59, 0.3);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px; /* 137.5% */
  }
  label:before {
    border-radius: 50px;
    width: 20px;
    height: 20px;
  }
  input:checked + label:after {
    width: 10px;
    height: 10px;
    left: 5px;
    top: 6px;
    border: 0px;
    background: #2d54c0;
    border-radius: 50%;
  }
}

/* custom radio */

.w-full {
  width: 100% !important;
  max-width: 100%;
  display: block !important;
}
.text-center {
  text-align: center;
}

.consent {
  p {
    color: rgba(7, 32, 59, 0.3);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 157.143% */
  }
  *:last-child {
    margin-bottom: 0px;
    padding: 0px;
  }
}

.label-group-head {
  display: block;
  width: 100%;
  position: relative;
  margin-bottom: 10px;
  p {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    padding: 0px;
  }
}

/* umbraco form styling */
.back-end-form {
  display: block;

  width: 100%;
  @media screen and (max-width: 1024px) {
    margin-bottom: 50px;
  }
  /* umbraco form field styles */
  p {
    padding: 0px;
  }
  .umbraco-forms-field {
    margin-bottom: 35px;
  }
  .umbraco-forms-form {
    padding: 0px;
    input.text,
    textarea,
    input[type="text"],
    input[type="email"],
    input[type="password"],
    select {
      border: 1px solid rgba(7, 32, 59, 0.2);
      background: rgba(255, 255, 255, 0.6);
      padding: 14px 25px;
      width: 100%;
      max-width: 100% !important;
      display: block;
      border-radius: 4px;
      margin: 0px;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 22px; /* 137.5% */
      @media (max-width: 1024px) {
        padding: 10px 15px;
      }
    }
    // radio btn and check box
    .checkboxlist,
    .radiobuttonlist {
      padding: 0px;
      margin-bottom: -20px;
      > div {
        margin-right: 30px;
      }
      label {
        margin-bottom: 0px !important;
      }
      // *:last-child {
      //   label {
      //     margin-bottom: 0px !important;
      //   }
      // }
    }

    .checkbox,
    .checkboxlist,
    .radiobuttonlist {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      max-width: 100%;

      > div {
        margin-bottom: 20px;
      }
      // > div:last-child {
      //   margin-bottom: 0px;
      // }
      input {
        padding: 0;
        height: initial;
        width: initial;
        margin-bottom: 0;
        display: none !important;
        cursor: pointer;
      }

      label {
        position: relative;
        cursor: pointer;
        color: #07203b;
        font-size: 14px;
        font-style: normal;
        font-weight: 400 !important;
        line-height: 20px; /* 125% */
        margin-bottom: 0px;
        padding-left: 30px;
      }
      input:checked + label {
        color: #07203b;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px; /* 137.5% */
      }
      label:before {
        content: "";
        -webkit-appearance: none;
        border: 1px solid rgba(7, 32, 59, 0.2);
        background: rgba(255, 255, 255, 0.6);
        border-radius: 0px;
        width: 20px;
        height: 20px;
        display: inline-block;
        position: relative;
        vertical-align: middle;
        cursor: pointer;
        margin-right: 10px;
        margin-top: -2px;
        position: absolute;
        top: 2px;
        left: 0px;
      }
      input:checked + label:before {
        border: 2px solid #2d54c0;
      }
      input:checked + label:after {
        content: "";
        display: block;
        position: absolute;
        top: 3px;
        left: 7px;
        width: 6px;
        height: 11px;
        border: solid #2d54c0;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
      }
    }
    .radiobuttonlist {
      ul {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        margin: -10px;
        margin-bottom: 0px;
        li {
          margin: 10px;
        }
      }
      label {
        color: #07203b;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px; /* 137.5% */
        padding-left: 33px;
        display: block;
      }
      label:before {
        border-radius: 50px;
        width: 20px;
        height: 20px;
        position: relative;
        top: 2px;
        left: 0px;
        position: absolute;
      }
      input:checked + label:after {
        width: 10px;
        height: 10px;
        left: 5px;
        top: 5px;
        border: 0px;
        background: #2d54c0;
        border-radius: 50%;
      }
    }
    label.umbraco-forms-label {
      font-weight: 500;
    }
    legend {
      // margin-bottom: 18px;
      // font-weight: 600;
      // font-size: 18px;
      margin-bottom: 23px;
      font-size: 18px;
      font-style: normal;
      line-height: normal;
      @media (max-width: 1024px) {
        font-size: 16px;
      }
    }
    legend.umbraco-forms-legend {
      color: var(--base-text-color);
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: 22px;
      letter-spacing: 0.16px;
      margin-bottom: 20px;
    }

    fieldset {
      padding: 0px !important;
      margin: 0px !important;
      margin-bottom: 35px !important;
    }
    fieldset + fieldset {
      margin-top: 35px !important;
    }
    .umbraco-forms-container {
      padding: 40px 40px 15px;
      background: #f9fbfd;
      border: 1px solid #ededed;
      @media (max-width: 1024px) {
        padding: 30px 20px 15px;
      }
    }
    .umbraco-forms-navigation {
      padding: 0px;
    }
    .umbraco-forms-caption {
      font-size: 24px !important;
      font-style: normal !important;
      line-height: 35px !important;
      margin-bottom: 35px;
      @media (max-width: 1024px) {
        font-size: 20px !important;
      }
    }
    input[type="submit"] {
      border: 0px;
      box-shadow: 0px;
      border-radius: 4px;
      background: #2d54c0;
      color: #fff;
      font-size: 16px;
      font-style: normal;
      font-weight: 600;
      line-height: 180%;
      padding: 11px 15px;
      min-width: 182px;
      float: right;
      transition: 0.4s ease;
      &:hover {
        background: rgba(45, 84, 192, 0.7);
      }
    }
  }
}
span.umbraco-forms-submitmessage {
  display: flex;
  width: 100%;
  text-align: center;
  border: 1px solid #f5f0f0;
  padding: 15px;
  font-size: 20px;
  font-weight: 600;
  border-radius: 7px;
  background: #e7f5ff;
  min-height: 300px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h1 {
    font-size: 25px;
  }

  p {
    font-size: 20px;
    font-weight: 600;
  }
}
