.navigation {
  display: block;
  width: 100%;
  max-width: 100%;
  .top-nav {
    display: block;
    width: 100%;
    max-width: 100%;
    padding: 22px 0px;
    position: relative;
  }
  &__block {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    max-width: 100%;
    @media (max-width: 1024px) {
      justify-content: space-between;
      max-width: calc(100% - 50px);
      padding-right: 20px;
    }
    .btn--login {
      @media (max-width: 1024px) {
        padding: 7px;
      }
      .btn-logo {
        @media (max-width: 1024px) {
          margin-right: 0px;
        }
      }
      .btn-text {
        @media (max-width: 1024px) {
          display: none;
        }
      }
    }
  }
  .logo-block {
    width: 100%;
    max-width: 108px;
    @media (max-width: 1024px) {
      max-width: 80px;
    }
  }
  .button-block {
    width: 100%;
    max-width: 265px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    @media (max-width: 1024px) {
      width: auto;
    }
  }
  .link-block {
    display: block;
    width: 100%;
    max-width: calc(100% - 373px);
    @media (max-width: 1024px) {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      max-width: 60%;
      position: fixed;
      top: 0;
      right: 0;
      background: var(--brand-color-primary);
      height: 100svh;

      padding: 50px 20px 15px;
      opacity: 0;
      visibility: hidden;
      overflow-y: auto;
      z-index: -1;
      transition: opacity 0.3s ease;
    }
    @media (max-width: 768px) {
      max-width: 98%;
    }
    &.is-active {
      @media (max-width: 1024px) {
        display: flex;
        opacity: 1;
        visibility: visible;
        z-index: 99;
      }
    }
  }
}
// navigation link styles

.link-block {
  nav {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding-left: 48px;
    @media (max-width: 1024px) {
      display: block;
      padding-left: 0px;
    }
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    margin: 0;
    flex: 1;

    margin: 0px -17px;
    @media screen and (max-width: 1260px) {
      margin: 0px -8px;
    }
    @media screen and (max-width: 1024px) {
      display: block;
      margin: 0px 0px;
      padding-left: 0px;
    }
    li {
      margin: 0px 17px;
      @media screen and (max-width: 1260px) {
        margin: 0px 8px;
      }
      @media screen and (max-width: 1024px) {
        margin: 0px 0px 30px;
      }
      a {
        color: var(--base-text-color);
        font-family: var(--primary-font);
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        transition: 0.4s ease;
        @media screen and (max-width: 1260px) {
          font-size: 16px;
        }
        @media (max-width: 1024px) {
          color: #fff;
        }
        &:hover {
          @media (min-width: 1024px) {
            color: #3674b5;
          }
        }
      }
    }
  }
}

// lang button
.lang-click {
  display: inline-flex;
  align-items: center;
  padding-right: 20px;
  &:after {
    content: "";
    width: 0px;
    height: 0px;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 6px solid #ababab;
    position: absolute;
    right: 0;
    top: 9px;
  }
}
.lang-btn {
  display: inline-flex;
  align-items: center;
  color: var(--base-text-color);
  font-family: var(--primary-font);
  margin-left: 15px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  transition: 0.4s ease;
  position: relative;
  // min-width: 100px;
  // max-width: 100px;
  @media screen and (max-width: 1260px) {
    font-size: 14px;
  }
  @media (max-width: 1024px) {
    margin-left: 15px;
  }
  svg {
    path {
      transition: 0.4s ease;
    }
  }
  .globe {
    margin-right: 4px;
    line-height: 0;
  }
  &:hover {
    color: #3674b5;
    svg {
      path {
        stroke: #3674b5;
      }
    }
  }
}

.main-nav {
  display: block;
  width: 100%;
  max-width: 100%;
  position: relative;
  background: var(--brand-color-primary);
  @media (max-width: 1024px) {
    display: none;
  }
  .main-nav-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    max-width: 100%;
  }

  nav {
    width: 100%;
    max-width: calc(100% - 210px);
    ul {
      padding-left: 0px;
      list-style: none;
      display: flex;
      flex-wrap: wrap;
      flex: 1;
      margin: -6px -16px;
      padding: 20px 0px;
      @media screen and (max-width: 1260px) {
        margin: -6px -8px;
      }
      li {
        margin: 6px 16px;
        @media screen and (max-width: 1260px) {
          margin: 6px 8px;
        }
        a {
          color: #fff;

          font-family: var(--primary-font);
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: normal;
          transition: 0.3s ease;
          position: relative;
          @media screen and (max-width: 1260px) {
            font-size: 14px;
          }
          &:after {
            @media (min-width: 1024px) {
              content: "";
              width: 0;
              height: 2px;
              background: #fff;
              bottom: -3px;
              left: auto;
              right: 0;

              position: absolute;
              transition: all 0.3s ease;
            }
          }
          &:hover {
            &:after {
              @media (min-width: 1024px) {
                content: "";
                width: 100%;
                right: auto;
                left: 0;
              }
            }
          }
        }
      }
    }
  }
}

.mobile-nav {
  @media (min-width: 1024px) {
    display: none;
  }
}

// hamberger styles
.hamburger {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  width: 32px;
  height: 27px;
  left: auto;
  z-index: 99;
  @media (min-width: 1024px) {
    display: none;
  }
  &.active {
    position: fixed;
    top: 30px;
    right: 25px;
  }
}
.hamburger .line {
  width: 100%;
  height: 3px;
  background-color: #000;
  display: block;
  margin: 5px auto;
  -webkit-transition: all 0s ease-in-out;
  -o-transition: all 0s ease-in-out;
  transition: all 0s ease-in-out;
}

.hamburger:hover {
  cursor: pointer;
}
#hamburger-1.is-active .line:nth-child(2) {
  opacity: 0;
}

#hamburger-1.is-active .line:nth-child(1) {
  -webkit-transform: translateY(13px) rotate(45deg);
  -ms-transform: translateY(13px) rotate(45deg);
  -o-transform: translateY(13px) rotate(45deg);
  transform: translateY(13px) rotate(45deg);
}

#hamburger-1.is-active .line:nth-child(3) {
  -webkit-transform: translateY(-3px) rotate(-45deg);
  -ms-transform: translateY(-3px) rotate(-45deg);
  -o-transform: translateY(-3px) rotate(-45deg);
  transform: translateY(-3px) rotate(-45deg);
}
#hamburger-1.is-active .line {
  background: #fff;
}
