/* TEN */

.social-icon {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: auto;
  max-width: 210px;
  padding-left: 30px;

  ul {
    display: flex;
    list-style: none;
    padding: 0px;
    margin: 0px -10px;

    li {
      margin: 0px 10px;
      transition: 0.3s ease;
      &:hover {
        opacity: 0.7;
      }
    }
  }
}
body.nav-open {
  overflow: hidden;
}

.lang-drop {
  position: absolute;
  top: calc(100% + 10px);
  left: 10px;
  border: 1px solid rgba(7, 32, 59, 0.2);
  width: 125px;
  left: 0;
  z-index: 2;
  background: #fff;
  display: none;
  &.active {
    display: block;
  }
}
.lang-drop ul {
  list-style: none;
  padding: 5px;
  margin: 0px;
}
.lang-drop ul li {
  text-align: left;
  padding: 6px 10px;
  transition: 0.4s ease;
  &.active {
    a {
      color: #3674b5;
    }
  }
  &:hover {
    background: #f1f1f1;
  }
}

.lang-drop ul li a {
  color: var(--base-text-color);
  font-family: var(--primary-font);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  transition: 0.4s ease;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .ln-icon {
    margin-left: 10px;
    font-size: 12px;
  }
  &:hover {
    color: #3674b5;
  }
}
