.service-card {
  display: block;
  width: 100%;
  max-width: 100%;
  position: relative;
  padding: 20px 50px 22px;
  @media (max-width: 1300px) {
    padding: 20px 25px 22px;
  }
  @media (max-width: 1024px) {
    padding: 15px;
  }
  &__icon {
    margin-bottom: 23px;
    img {
      width: auto;
      max-width: 60px;
      height: auto;
    }
  }
  &__content {
    display: block;
    h3 {
      transition: 0.4s ease;
      @media (max-width: 1024px) {
        font-size: 16px;
      }
    }
  }
  .link-arrow {
    position: absolute;
    right: 25px;
    top: 31px;
    opacity: 0;
    visibility: hidden;
    transition: 0.4s ease;
  }
}

.service-item {
  &:hover {
    .service-card__content {
      h3 {
        color: #3674b5;
      }
    }
    .link-arrow {
      opacity: 1;
      visibility: visible;
    }
  }
}
