.service {
  padding: 50px 0px;
  &__wrapper {
    display: block;
    width: 100%;
    max-width: 1319px;
  }
  &__head {
    display: block;
    margin-bottom: 20px;
    *:last-child {
      margin-bottom: 0px;
    }
  }
  .service__layout {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    position: relative;
    overflow: hidden;
  }
  .service-row {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    margin: -1px;
    margin-left: 0px;
    &:last-child {
      border-bottom: 0px;
    }
  }
  .service-item {
    display: block;
    width: 100%;
    max-width: calc((100% / 4));
    min-height: 50px;
    background: #fff;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    @media (max-width: 992px) {
      max-width: calc((100% / 3));
    }
    @media (max-width: 767px) {
      max-width: calc((100% / 2));
    }
    @media (max-width: 420px) {
      max-width: calc((100% / 1));
    }
    &:nth-child(4n + 1) {
      @media (min-width: 993px) {
        .service-card {
          padding-left: 0px;
        }
      }
    }
    &:nth-child(3n + 1) {
      @media (max-width: 992px) {
        .service-card {
          padding-left: 0px;
        }
      }
      @media (max-width: 767px) {
        .service-card {
          padding-left: 15px;
        }
      }
    }
    &:nth-child(2n + 1) {
      @media (max-width: 767px) {
        .service-card {
          padding-left: 0px;
        }
      }
      @media (max-width: 420px) {
        .service-card {
          padding-left: 15px;
        }
      }
    }

    &:nth-child(1n + 1) {
      @media (max-width: 420px) {
        .service-card {
          padding-left: 0px;
        }
      }
    }
  }
}
