@use "abstracts/mixins" as mixins;

.font {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: bold;
  margin-bottom: 10px;
  font-family: var(--primary-font);
  color: var(--base-text-color);
  line-height: normal;
}

h1,
.title-1 {
  @include mixins.font-size-rem(68);
  font-style: normal;
  line-height: normal;
}

h2,
.title-2 {
  @include mixins.font-size-rem(34);
  font-style: normal;
  line-height: normal;
  @media (max-width: 1024px) {
    font-size: 26px;
  }
}

h3,
.title-3 {
  @include mixins.font-size-rem(20);
  font-style: normal;
  line-height: normal;
}

h4,
.title-4 {
  @include mixins.font-size-rem(22);
  font-style: normal;
  line-height: normal;
}

h5,
.title-5 {
  @include mixins.font-size-rem(18);
  font-style: normal;
  line-height: normal;
}

h6,
.title-6 {
  @include mixins.font-size-rem(15);
  font-style: normal;
}
.font-48 {
  font-weight: bold;
  margin-bottom: 10px;
  font-family: var(--primary-font);
  color: var(--base-text-color);
  line-height: normal;
  @include mixins.font-size-rem(48);
  @media (max-width: 1024px) {
    font-size: 32px;
  }
}
.font-24 {
  font-family: var(--primary-font);
  color: var(--base-text-color);
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px; /* 116.667% */
  letter-spacing: -0.48px;
}

p {
  padding-bottom: 15px;
  font-family: var(--primary-font);
  color: var(--base-text-color);
}

input,
button,
* {
  font-family: var(--primary-font);
}
