.inner-banner {
  display: block;
  position: relative;
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__wrapper {
    display: flex;
    position: relative;
    z-index: 1;
    padding: 50px 0px 50px;
    min-height: 238px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @media (max-width: 1024px) {
      min-height: initial;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      color: #fff;
      text-align: center;
      font-weight: 400;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      margin-bottom: 0px;
    }
  }
  &.inner-banner-small {
    .inner-banner__wrapper {
      padding: 27px 0px;
      min-height: initial;
    }
  }
}

// inner banner pagination
.pagination {
  display: block;
  width: 100%;
  padding-right: 30px;
  ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    margin: -10px -20px;
    justify-content: flex-start;
    li {
      margin: 10px 20px;
      position: relative;
      a {
        color: #fff;

        text-align: center;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        text-decoration-line: underline;
        text-decoration-style: solid;
        text-decoration-skip-ink: auto;
        text-decoration-thickness: auto;
        text-underline-offset: auto;
        text-underline-position: from-font;
        @media (max-width: 1024px) {
          font-size: 14px;
        }
      }
      &:after {
        content: "";
        display: block;
        position: absolute;
        background-image: url("../images/pagination-arrow.svg");
        width: 8px;
        height: 14px;
        position: absolute;
        right: -24px;
        top: 57%;
        transform: translateY(-50%);
      }
      &:last-child {
        &:after {
          display: none;
        }
      }
      &.active {
        a {
          color: rgba(255, 255, 255, 0.5);

          text-align: center;
          font-style: normal;
          font-weight: 400;
          line-height: 120%; /* 19.2px */
        }
      }
    }
  }
}
