/* custom check box */
.custom-checkbox,
.custom-radio {
  display: block;
}
.custom-checkbox input,
.custom-radio input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}
.custom-checkbox label,
.custom-radio label {
  position: relative;
  cursor: pointer;
  color: rgba(7, 32, 59, 0.3);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 125% */
  margin-bottom: 0px;
}
.custom-checkbox input:checked + label,
.custom-radio input:checked + label {
  color: #07203b;
  font-size: 14px !important;
  font-style: normal;
  font-weight: 400 !important;
  line-height: 22px !important; /* 137.5% */
}
.custom-checkbox label:before,
.custom-radio label:before {
  content: "";
  -webkit-appearance: none;
  border: 2px solid rgba(7, 32, 59, 0.2);
  background: rgba(255, 255, 255, 0.6);
  border-radius: 2px;
  width: 20px;
  height: 20px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 10px;
  margin-top: -2px;
}
.custom-checkbox input:checked + label:before,
.custom-radio input:checked + label:before {
  border: 2px solid #2d54c0;
}
.custom-checkbox input:checked + label:after,
.custom-radio input:checked + label:after {
  content: "";
  display: block;
  position: absolute;
  top: 5px;
  left: 7px;
  width: 6px;
  height: 11px;
  border: solid #2d54c0;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.custom-radio ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: -10px;
  margin-bottom: 0px;
}
.custom-radio ul li {
  margin: 10px;
}
.custom-radio label {
  color: rgba(7, 32, 59, 0.3);
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px; /* 137.5% */
}
.custom-radio label:before {
  border-radius: 50px;
  width: 20px;
  height: 20px;
}
.custom-radio input:checked + label:after {
  width: 10px;
  height: 10px;
  left: 5px;
  top: 6px;
  border: 0px;
  background: #2d54c0;
  border-radius: 50%;
}

