.focus-card {
  display: block;
  width: 100%;
  max-width: 100%;
  position: relative;
  .media-block {
    margin-bottom: 20px;
    overflow: hidden;
    img {
      width: 100%;
      height: auto;
      transition: 0.4s ease;
    }
  }
  .focus-card__content {
    h3 {
      margin-bottom: 3px;
      transition: 0.4s ease;
      @media (max-width: 1024px) {
        font-size: 18px;
      }
    }
  }
  &:hover {
    .focus-card__content {
      h3 {
        color: #3674b5;
      }
    }
  }
}
