@use "abstracts/mixins" as mixins;
.popular-services {
  position: relative;
  width: 100%;
  display: flex;
  margin: 80px 0;
  flex-wrap: wrap;
}

.half-col {
  width: 50%;
}

.outer-layer {
  position: absolute;
  width: 100%;
  overflow: hidden;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  .half-col {
    float: right;
    img {
      height: 100%;
      object-fit: cover;
      width: 100%;
    }
  }
}

.content-layer {
  position: relative;
  width: 100%;
  z-index: 1;
  .half-col {
    float: left;
    padding-right: 50px;
  }
  .p-services-listing {
    display: flex;
    flex-flow: row wrap;
    margin: 0 -34px;
    padding: 30px 0 60px;
  }

  .service-item {
    width: 50%;
    padding: 20px 34px;
    transition: all 0.3s ease;
    i {
      margin-bottom: 10px;
      display: inline-block;
    }
    h3 {
      @include mixins.font-size-rem(20);
      font-weight: 600;
      line-height: normal;
      color: #07203b;
      margin-bottom: 8px;
    }
    p {
      @include mixins.font-size-rem(16);
      color: #07203b;
      margin-bottom: 0;
    }
    &:hover {
      h3 {
        color: #3674b5;
      }
    }
  }
}
@media only screen and (max-width: 1024px) {
  .content-layer {
    margin-top: 30px;
    h2 {
      @include mixins.font-size-rem(26);
    }
    .half-col {
      padding-right: 0;
    }
    .p-services-listing {
      margin: 0 -15px;
      padding: 20px 0 20px;
    }
    .service-item {
      padding: 10px 15px;
      h3 {
        @include mixins.font-size-rem(16);
      }
      p {
        @include mixins.font-size-rem(14);
      }
    }
  }
}
@media only screen and (max-width: 767px) {
  .popular-services {
    margin: 20px 0;
  }
  .outer-layer {
    position: relative;
  }
  .half-col {
    width: 100%;
  }
}
