@use "abstracts/mixins" as mixins;

$image-path: "../images/";

.site-footer {
  background-color: var(--base-text-color);
  position: relative;
  color: var(--text-white);
  padding: 50px 0 30px;

  .site-footer-top {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 30px;
  }

  // Left side content
  &-left {
    width: 50%;
    padding-right: 15px;

    p {
      @include mixins.font-size-rem(14);
      line-height: 160%;
      font-weight: 400;
      color: white;
    }

    &-inner {
      max-width: 610px;
      color: white;
    }
  }

  .footer-logo {
    h2 {
      color: var(--text-white);
      @include mixins.font-size-rem(28);
      font-weight: 700;
    }
  }

  // Right side content
  &-right {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 -15px;

    .footer-col {
      width: calc(100% / 3);
      padding: 0 15px;
    }
  }

  h5 {
    color: #8898a9;
    font-size: 1rem;
    font-weight: 500;
    line-height: normal;
  }

  ul {
    margin: 0;
    padding: 0;

    li {
      list-style: none;
      padding: 3px 0;
      margin: 0;
    }

    a {
      color: var(--text-white);
      @include mixins.font-size-rem(14);
      font-weight: 500;
      line-height: normal;
      transition: all 0.3s ease-in-out;

      &:hover,
      &:focus {
        color: #3674b5;
      }
    }
  }

  &-bottom {
    border-top: 1px solid rgba(35, 74, 117, 0.4);
    text-align: center;
    padding-top: 10px;

    p {
      @include mixins.font-size-rem(14);
    }
  }
  @media (max-width: 1024px) {
    &-left,
    &-right {
      width: 100%;
    }
    &-left {
      margin-bottom: 30px;
    }
  }
  @media (max-width: 768px) {
    &-left,
    &-right {
      width: 100%;
    }

    &-right {
      .footer-col {
        width: 50%;
        margin-bottom: 30px;
        padding: 0 15px;
      }
    }
  }
}

.social-links {
  position: relative;
  ul {
    display: flex;
    margin: 0;
    li {
      list-style: none;
      &:not(:last-child) {
        margin-right: 16px;
      }
    }
  }

  a {
    display: inline-block;
    span {
      display: none;
    }
    transition: all 0.3s;
    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }

  i {
    width: 20px;
    height: 20px;
    display: block;
    &.icon-fb {
      background: url(#{$image-path}/icon-facebook.svg) no-repeat;
    }
    &.icon-yt {
      background: url(#{$image-path}/icon-youtube.svg) no-repeat;
    }
    &.icon-ig {
      background: url(#{$image-path}/icon-instagram.svg) no-repeat;
    }
    &.icon-x {
      background: url(#{$image-path}/icon-x.svg) no-repeat;
    }
    &.icon-tel {
      background: url(#{$image-path}/icon-send.svg) no-repeat;
    }
  }
}
.search-box {
  padding-top: 20px;
  position: relative;
  p {
    padding-bottom: 0;
  }
  form {
    padding-top: 10px;
  }
  input {
    width: 100%;
    border: 1px solid white;
    padding: 16px;
    background: transparent;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    padding-left: 45px;
    background: url(#{$image-path}/icon-search.svg) no-repeat;
    background-position: 10px;
    color: white;
    @include mixins.font-size-rem(14);

    &::placeholder {
      color: white;
      opacity: 1;
      @include mixins.font-size-rem(14);
    }
  }
}
