.sidebar-widget {
  display: block;
  width: 100%;
  max-width: 100%;
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    li {
      border-bottom: 1px solid rgba(35, 74, 117, 0.2);
      margin-bottom: 21px;
      padding-bottom: 10px;
      a {
        color: #234a75;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        padding-right: 40px;
        position: relative;
        display: block;
        transition: 0.4s ease;
        .icon {
          svg {
            transition: 0.4s ease;
          }
        }
        &:hover {
          color: #3674b5;
          .icon {
            svg {
              transform: rotate(360deg);
              path {
                stroke: #3674b5;
              }
            }
          }
        }
      }
      .icon {
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
