.payment {
  display: block;
  position: relative;
  margin: 81px 0px;
  @media (max-width: 1024px) {
    margin: 40px 0px;
  }
  &__box {
    margin: 0 auto;
    max-width: 572px;
    border-radius: 8px;
    border: 1px solid rgba(7, 32, 59, 0.2);
    padding: 59px 48px 24px;
    @media (max-width: 1024px) {
      padding: 35px 25px 15px;
    }
    @media (max-width: 575px) {
      padding: 35px 15px 15px;
    }
  }
  &__form-head {
    padding-bottom: 15px;
    margin-bottom: 30px;
    border-bottom: 1px solid rgba(172, 172, 172, 0.2);
    *:last-child {
      margin: 0px;
      padding: 0px;
    }
  }
  label {
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px; /* 122.222% */
    @media (max-width: 1024px) {
      font-size: 16px;
    }
  }
  .custom-checkbox label,
  .custom-radio label {
    font-size: 14px;
    line-height: 22px; /* 125% */
  }
  .single-col-form {
    margin-bottom: 27px;
  }
  .custom-checkbox {
    margin-top: -8px;
  }
  .single-col-form .btn {
    margin-top: 7px;
  }
}
